@import "https://fonts.googleapis.com/css2?family=Roboto&display=swap";
html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  overflow: hidden;
}

body {
  background: #f3f8fd url("background.5c9f5a85.jpg");
  background: url("background.5c9f5a85.jpg") -moz-linear-gradient(top, #f3f8fd 0%, #fff 100%);
  background: url("background.5c9f5a85.jpg") -webkit-linear-gradient(top, #f3f8fd 0%, #fff 100%);
  background: url("background.5c9f5a85.jpg") linear-gradient(to bottom, #f3f8fd 0%, #fff 100%);
  background-size: cover;
}

.btn, #copyright {
  position: fixed;
}

.btn {
  z-index: 999;
  width: 80px;
  box-sizing: border-box;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  cursor: pointer;
  background: #000;
  border-radius: 15px;
  padding: 15px;
  font-size: 24px;
  transition: all .2s;
}

.flip-container {
  width: 4em;
  height: 2em;
  background: linear-gradient(#004001 0%, #24ed1c 100%);
  border-bottom: 5px solid green;
  border-radius: 0 0 15px;
}

.flip-container i.fa.noselect {
  position: relative;
  top: -.2em;
}

.flip-container {
  border-radius: 0 0 0 15px;
  right: 0;
}

.snapshot-container {
  width: 4em;
  height: 4em;
  background: #ed7d1c;
  border: 10px solid #fff;
  border-radius: 100%;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: inset 0 0 10px #00000080;
}

.snapshot-container i.fa.noselect {
  line-height: 2em;
}

.btn:hover {
  background: orange;
}

.btn:active {
  background-color: gray;
}

.flip-container:hover {
  width: 5em;
  background: linear-gradient(#004001 0%, #24ed1c 100%);
}

#ZapparSnapshotContainer {
  background: linear-gradient(#20be19 0%, #002401 100%);
}

#zapparSaveButton .st0, #zapparShareButton .st0 {
  fill: #fff;
}

#zapparCloseAref g {
  stroke: #fff;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  -khtml-user-select: none;
}

